export type StatKey = "" | "" | "" | "" | "" | "";

export class Monster {
    id: string;
    name: string;
    actionIds: string[];
    stats: Record<StatKey, number>;

    constructor(id: string, name: string, stats: Record<string, number>, actionIds: string[] = []) {
        this.id = id;
        this.name = name;
        this.stats = stats;
        this.actionIds = actionIds;
    }

    getModifier(statKey: StatKey) {
        return Math.floor((this.stats[statKey] - 10) / 2);
    }

    toShowString() {
        return `${this.name} - : ${this.stats['']} : ${this.stats['']} : ${this.stats['']} : ${this.stats['']} : ${this.stats['']} : ${this.stats['']}`;
    }
}
